import { makeStyles } from "@material-ui/core";
import { observer } from "mobx-react";
import React from "react";
import { ILendingNetworkGist } from "../../services/lendingNetworksService/ILendingNetworksService";
import { NetworkListItem } from "./NetworkListItem";
import { SingleLNStore } from "../../store/multiInstanceStores/SingleLNStore";

const useStyles = makeStyles(({ spacing }) => ({
  root: {
    display: "flex",
    // flexDirection: "column",
    // alignItems: "center",
    margin: spacing(1, 8),
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    marginRight: "auto",
    marginLeft: "auto",
  },
}));

interface IProps {
  // lendingNetworksGists: ILendingNetworkGist[];
  lendingNetworkStores: SingleLNStore[];
  onViewNetwork: (networkId: string) => void;
}

export const NetworksList: React.FC<IProps> = observer((props) => {
  const classes = useStyles();
  const { lendingNetworkStores, onViewNetwork } = props;

  return (
    <div className={classes.root}>
      <div
        style={{
          width: "100%",
          display: "grid",
          gridTemplateColumns: "repeat(auto-fit, minmax(15rem, 1fr)",
          gridGap: 15,
          padding: 15,
          justifyItems: "center",
          justifyContent: "center",
        }}
      >
        {lendingNetworkStores.map((lnStore) => (
          <NetworkListItem
            onViewNetwork={onViewNetwork}
            lnStore={lnStore}
            key={lnStore.id}
          />
        ))}
      </div>
    </div>
  );
});
