import React, { SVGProps } from "react";
import { makeStyles } from "@material-ui/core";
import { AFFIRMATIVE_GREEN_COLOR } from "../../theme/themeConstants";
import clsx from "clsx";

interface IProps {
  dimension: number;
  strokeWidth: number;
  strokeWidthFactor: number;
  strokeColor: string;

  // 0%-100%
  fillPercentage: number;

  antiClockwise?: boolean;

  svgContainerClass?: string;
  svgCircleClass?: string;
}

const useStyles = makeStyles((theme) => ({
  completionCircle: {
    // stroke: "white",
    stroke: AFFIRMATIVE_GREEN_COLOR,
    transition: "stroke-dashoffset 0.35s",

    transform: "rotate(-90deg)",
    transformOrigin: "50% 50%",
  },
}));

export const CompletionCircle = React.memo<IProps & SVGProps<SVGSVGElement>>(
  (props) => {
    const classes = useStyles();
    const {
      dimension,
      strokeWidth,
      strokeWidthFactor,
      strokeColor,
      fillPercentage,
      antiClockwise,
      svgCircleClass,
      svgContainerClass,
      ...svgProps
    } = props;

    const height = dimension;
    const width = dimension;
    const centerOffset = dimension / 2;
    const radius = width / 2 - strokeWidth * 2;
    const circumference = radius * 2 * Math.PI;
    const calculationOffset =
      circumference - (fillPercentage / 100) * circumference;
    const offSetByDirection = antiClockwise
      ? -calculationOffset
      : calculationOffset;

    const strokeWidthToUSe = strokeWidth * strokeWidthFactor;

    return (
      <svg height={height} width={width} className={svgContainerClass}>
        <circle
          className={clsx([classes.completionCircle, svgCircleClass])}
          stroke-width={strokeWidthToUSe}
          fill={"transparent"}
          r={radius}
          cx={centerOffset}
          cy={centerOffset}
          style={{
            // strokeWidth: strokeWidth,
            strokeDashoffset: offSetByDirection,
            strokeDasharray: `${circumference} ${circumference}`,
            stroke: strokeColor,
          }}
        />
      </svg>
    );
  }
);
